.row{
    margin-left: 20px;
    color: white;
}
.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row__posters::-webkit-scrollbar{
    display: none;
}
.row__poster{
    width: 100%;
    object-position: contain;
    max-height: 100px;
    transition: transform 450ms;
    margin-right: 10px;

}
.row__poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row__posterLarge{
    max-height: 250px;

}
.row__posterLarge:hover{
    transform: scale(1.09);
  
}
.netflix{
    background-color: #111;
}


.banner{
    color: white;
    object-fit: contain;
    height: 448px;
   

}

.banner__contents{
    margin-left: 30px;
    padding: 140px;
    height: 190px;
}

.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner__button{
    cursor: pointer;
    color: #fff;
    outline: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-left: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom: 0.5rem;
}
.banner__button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}
.banner--fadeBottom{
    height: 7.4rem;
    background-image:linear-gradient(
        180deg,
        tranparent,
        rgba(37,37,37,0.61),
        #111

    );
}

.nav{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    background-color: #111;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__black{
    background-color: #111;
}
.nav__logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}
.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}